export default {
	data() {
		return {
			title: 'Hello',
			orderNo:'',
			billDetail:null
		}
	},
	onLoad(option) {
		if(option.orderNo){
			this.orderNo = option.orderNo;
			this.detail();
		}
	},
	methods: {
		
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		detail(){
			let _this = this;
			this.$ajax("/order/detail","POST",{orderNo:_this.orderNo}).then((res)=>{
				_this.billDetail = res
			})
		}
	},
	filters:{
		position(value){
			if(value == -1){
				return "取消订单";
			}
			if(value == 1){
				return "待支付";
			}
			if(value == 2){
				return "待发货";
			}
			if(value == 3){
				return "待收货";
			}
			if(value == 0){
				return "交易成功";
			}
			if(value == -1){
				return "取消订单";
			}
		},
		size(value){
			let sizeArr = JSON.parse(value);
			let size = "";
			sizeArr.map((res)=>{
				size = size + " " + res.value 
			})
			return size;
		},
		dateTime(value){
			let dateArr = [];
			dateArr = value.split(".");
			let dateTime = dateArr[0].replace("T"," ");
			return dateTime;
		},
		getP(s){
			this.$ajax("/order/finish","POST",{orderNo:billDetail.orderNo}).then(res=>{
				if(res.code == "200"){
					uni.showToast({
						"title":"确认收货成功",
						"icon":"none"
					})
					setTimeout(function(){
						uni.reLaunch({
							"url":"/pages/mine/bill"
						})
					},888)
					
				}
			})
		},
	}
}
